<template>
  <div class="device-info" ref="host">
    <router-view ></router-view>
  </div>
</template>

<script>
  export default {
    name: "device-info",
    provide(){
      return {
        reload:this.reload
      }
    },
    data(){
      return{
        isRouterAlive: true
      }
    },
    methods: {
      reload () {
        this.isRouterAlive = false
        this.$nextTick(() => (this.isRouterAlive = true))
      }
    },
    mounted(){
      this.$eventHub.$on('device-info-to-top',()=>{
        if(this.$refs.host){
          this.$refs.host.scrollTop=0;
        }
      })
    }
  }
</script>

<style lang="less" scoped>
  .device-info {
    /*height: auto;*/
   overflow: auto;
    min-height: 100%;
    /*padding: 20px;*/
  }
</style>



